import React, { useContext } from "react";
import styled from "styled-components";
import { Container, Typography, TextField, Button, Grid } from "@material-ui/core";
import { CartContext } from '../contexts/CartContext';
import minusIcon from "../assets/subtract-line.svg";
import plusIcon from "../assets/plus.svg";
import binIcon from "../assets/delete-bin-5-fill.svg";

const ProductInCart = (props) => {
    const { increase, decrease, removeProduct } = useContext(CartContext);
    const handleIncreaseProduct = () => {
        increase(props.product);
    }
    
    const handleDecreaseProduct = () => {
        if(props.product.quantity > 1){
            decrease(props.product);
        }
        else{
            removeProduct(props.product);
        }
    }

    const handleRemoveProduct = () => {
        removeProduct(props.product);
    }

    return (
        <Grid container wrap="nowrap" spacing={2}>
            <Grid item>
                <ButtonWrapper onClick={handleRemoveProduct}><RemoveImg src ={binIcon}/></ButtonWrapper>
            </Grid>
            <Grid item >
                <ImageWrapper src={"https://placeralplato.com/files/2016/01/Pizza-con-pepperoni-640x480.jpg?width=1200&enable=upscale"}></ImageWrapper>
            </Grid>
            <Grid item>
                <ButtonWrapper onClick={handleDecreaseProduct}><DecreaseImg src ={minusIcon}/></ButtonWrapper>
            </Grid>
            <Grid item>
                <ProductName>{props.product.quantity}</ProductName>
            </Grid>
            <Grid item>
                <ButtonWrapper onClick={handleIncreaseProduct}><AddImg src ={plusIcon}/></ButtonWrapper>
            </Grid>
            <Grid item xs zeroMinWidth>
                <ProductName noWrap>{props.product.name}</ProductName>
            </Grid>
            <Grid item>
                <ProductName>{"$"+props.product.price*props.product.quantity}</ProductName>
            </Grid>
        </Grid>
    );
};

const ProductName = styled(Typography)`
 &&{
  
  color:white;
 }`

 const DecreaseImg = styled.img`
    width: 30px;
    height: 30px;
`

const AddImg = styled.img`
    width: 20px;
    height: 20px;
`

const RemoveImg = styled.img`
    width: 30px;
    height: 40px;
`

 const ImageWrapper = styled.img`
    width: 50px;
    height: 50px;
    border-radius: 50%;
 
`
const ButtonWrapper = styled.button`
    background:none;
    border:none;
`
export default ProductInCart;